import React, { useState } from 'react'
import classNames from 'classnames'

import { alpha, IconButton, makeStyles, Typography, withStyles } from '@material-ui/core'
import { ChevronLeft, ChevronRight } from '@material-ui/icons'
import { TreeItem, TreeView } from '@material-ui/lab'
import { FiMinusSquare, FiPlusSquare } from 'react-icons/fi'

const useStyles = makeStyles((theme) => ({
  root: {
    flex: '1',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'stretch',
    backgroundColor: theme.palette.background.paper,
    borderRight: '1px solid',
    borderRightColor: theme.palette.divider,
    width: '80px',
    transition: 'width .3s'
  },
  fullWidthRoot: {
    width: theme.breakpoints.values.sm,
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: theme.spacing(2),
    borderBottom: '1px solid',
    borderBottomColor: theme.palette.divider,
  },
  content: {
    display: 'flex',
    flex: '1 1 auto',
    minHeight: '0px',
    overflowY: 'auto'
  },
  treeView: {
    flex: 1,
    padding: theme.spacing(3)
  }
}))

const StyledTreeItem = withStyles((theme) => ({
  label: {
    padding: theme.spacing(1),
    borderRadius: '3px'
  },
  iconContainer: {
    '& .close': {
      opacity: 0.3,
    },
  },
  group: {
    marginLeft: 7,
    paddingLeft: 18,
    borderLeft: `1px dashed ${alpha(theme.palette.text.primary, 0.4)}`,
  },
}))((props) => <TreeItem onLabelClick={(e) => e.preventDefault()} {...props} />)

const NavigationPane = () => {
  const classes = useStyles()
  const [isCollapsed, setIsCollapsed] = useState(false)

  return (
    <div className={classNames(classes.root, !isCollapsed && classes.fullWidthRoot)}>
      <div className={classes.header}>
        {
          !isCollapsed && <Typography variant='h3'>Navigation Pane</Typography>
        }
        <IconButton onClick={() => setIsCollapsed((prev) => !prev)}>
          {
            isCollapsed
              ? <ChevronRight />
              : <ChevronLeft />
          }
        </IconButton>
      </div>
      <div className={classes.content}>

        <TreeView
          hidden={isCollapsed}
          className={classes.treeView}
          defaultCollapseIcon={<FiMinusSquare />}
          defaultExpandIcon={<FiPlusSquare />}
        >
          <StyledTreeItem nodeId="1" label="Applications">
            <StyledTreeItem nodeId="2" label="Calendar" />
            <StyledTreeItem nodeId="3" label="Chrome" />
            <StyledTreeItem nodeId="4" label="Webstorm" />
          </StyledTreeItem>
          <StyledTreeItem nodeId="5" label="Documents">
            <StyledTreeItem nodeId="10" label="OSS" />
            <StyledTreeItem nodeId="6" label="Material-UI">
              <StyledTreeItem nodeId="7" label="src">
                <StyledTreeItem nodeId="8" label="index.js" />
                <StyledTreeItem nodeId="9" label="tree-view.js" />
              </StyledTreeItem>
            </StyledTreeItem>
          </StyledTreeItem>
        </TreeView>
      </div>
    </div >
  )
}

export default NavigationPane